import './App.css';
import {posts} from './posts.js'
import POSTS from './components/post'

function App() {

  let postCategory = 'hack_participants'

  const posts_list = posts.map((post, i) => {
    if (postCategory === post['postCategory']) {
      return <POSTS title={post['title']} description={post['description']} postUrl={post['url']} id={i} />
    }
  });

  return (
    <div className="App">


      <div>
        <video autoPlay muted loop id="bg-video">
          <source src={"../videos/bg_video.mp4"} type="video/mp4" allow="autoplay;" />
        </video>

        
      </div>


      <div className="main d-flex">
        
        <div className="info-box col-md-4">
          <h1>Create your custom Post</h1>
          <p>Let your friends know that you are participating at START Summit x Hack. Take the opportunity to create your own personalized post and share it on Instagram, LinkedIn or Facebook.</p>
        </div>
        {posts_list}
        
        

      </div>
      <div className="disclaimer">Your personalized post will be generated directly in your browser, therefore, your pictures will not be uploaded to nor be stored on any server.</div>
    </div>
  );
}

export default App;
