import React from 'react';
import fetch from 'isomorphic-fetch';
import Resizer from 'react-image-file-resizer';
import './post.css';

const {saveSvgAsPng, svgAsPngUri} = require('save-svg-as-png')


const imageOptions = {
  scale: 1,
  encoderOptions: 1,
  backgroundColor: 'white',
}



class Post extends React.Component {


    constructor(props) {    
        super(props);
        this.state = {imageUploaded: false, image: null, postImage: null, rangeValue: 1, imgScale: [null, null]}
    }

    componentDidMount() {
      this.loadSVG();
    };


    handleClick = () => {
      
      if (this.state.imageUploaded === true) {
        var svg = document.getElementById("svg-image-" + this.props.id);
        svgAsPngUri( svg, imageOptions ).then( () => saveSvgAsPng(svg, "starthack.png") );
      }
      
    };

    loadSVG() {

      fetch(this.props.postUrl)
        .then(
          response => {
            if (!response.ok) return Promise.reject(new Error('Server Error'));
            return response.text();
          }
        ).then(
          svg => {
            svg = svg.replaceAll(/<svg width="([0-9]*?)" height="([0-9]*?)"/g, '<svg id="svg-image" width="100%" height="100%"');
            
            
            //var identifiers = (svg.match(/(?<=id=").*?(?=")/g))

            //vvv Added to support Safari vvv
            var identifiers = (svg.match(/(?:id=").*?(?=")/g))
            
            for (var i = 0; i < identifiers.length; i++) {
              //vvv Added to support Safari vvv
              identifiers[i] = identifiers[i].replace("id=\"", "")
              svg = svg.replaceAll(identifiers[i], identifiers[i] + '-' + this.props.id);
            }

            this.setState({ postImage: svg});

            var pattern = document.getElementById("replace-pattern-" + this.props.id)
            
            //var scale = String(pattern.innerHTML.match(/(?<=scale\()[0-9\.+-\s]+/g)).split(" ")
            
            //vvv Added to support Safari vvv
            var scale = String(pattern.innerHTML.match(/(?:scale\()[0-9\.+-\s]+/g))
            scale = scale.replace("scale(", "").split(" ")
            
            var scaleX = scale[0]
            var scaleY = scale[scale.length - 1]
            scale = [scaleX, scaleY]
            
            var rangeValue = 1
            this.setState({ 
              imgScale: scale,
              rangeValue: rangeValue,
            });

            pattern.children[0].setAttribute("transform",'translate(' + (1-rangeValue)/2 + ' ' + (1-rangeValue*scale[1]/scale[0])/2 + ') scale(' + scaleX * rangeValue + ' ' + scaleY * rangeValue + ')')
      


          } 
          
        );

    };

    changeSVG(image) {
      fetch(this.props.postUrl)
        .then(
          response => {
            if (!response.ok) return Promise.reject(new Error('Server Error'));
            return response.text();
          }
        ).then(
          svg => {

            svg = svg.replaceAll(/<svg width="([0-9]*?)" height="([0-9]*?)"/g, '<svg id="svg-image" width="100%" height="100%"');
            
            //var identifiers = (svg.match(/(?<=id=").*?(?=")/g))
            //vvv Added to support Safari vvv
            var identifiers = (svg.match(/(?:id=").*?(?=")/g))
            for (var i = 0; i < identifiers.length; i++) {
              //vvv Added to support Safari vvv
              identifiers[i] = identifiers[i].replace("id=\"", "")
              svg = svg.replaceAll(identifiers[i], identifiers[i] + '-' + this.props.id);
            }

            var width = document.getElementById("replace-image-" + this.props.id).width.baseVal.value
            var height = document.getElementById("replace-image-" + this.props.id).height.baseVal.value
            
            
            var imageData = '<image id="replace-image-'+this.props.id+'" width="' + width + '" height="' + height + '" xlink:href="' + image + '"'
            svg = svg.replaceAll(/<image id="replace-image(.*?)xlink:href="(.*?)"/g , imageData)


            var pattern = document.getElementById("replace-pattern-" + this.props.id)
            
            
            var scale = this.state.imgScale

            var rangeValue = 1
            this.setState({ 
              postImage: svg,
              rangeValue: 1,
            });


            
            pattern.children[0].setAttribute("transform",'translate(' + (1-rangeValue)/2 + ') scale(' + scale[0] * rangeValue + ' ' + scale[1] * rangeValue + ')')
      
            
          } 
        );


        
    }

    handleChangeScale = (e) => {
      this.setState({rangeValue: e.target.value});

      var pattern = document.getElementById("replace-pattern-" + this.props.id)
      
      pattern.children[0].setAttribute("transform",'translate(' + (1-this.state.rangeValue)/2 + ' ' + (1-this.state.rangeValue*this.state.imgScale[1]/this.state.imgScale[0])/2 + ') scale(' + this.state.imgScale[0] * this.state.rangeValue + ' ' + this.state.imgScale[1] * this.state.rangeValue + ')')
      
    };


    handleImageChange = (e) => {

      
      var file = e.target.files[0];

      try {
        Resizer.imageFileResizer(
        file,
        600,
        600,
        'PNG',
        100,
        0,
        uri => {

            
            this.setState({
              imageUploaded: true,
              image: uri,
              rangeValue: 1,
            });
            this.changeSVG(uri)
        },
        'base64',
        600,
        600,
        );
      }   catch(err) {
              console.log(err)
      }

      
    };


    render() {
        
        return (
          <div className="d-flex">
            <div className="card m-4" style={{"width": "18rem"}}>
              
              <div className="card-img-top post-img" dangerouslySetInnerHTML={{__html: this.state.postImage}}></div>
              
              <div className="card-body">
                <div>
                  <p className="post-description" dangerouslySetInnerHTML={{__html: this.props.description}}></p>
                </div>

                <div className="d-flex flex-no-wrap d-flex justify-content-center align-items-center">
                  <i className="fas fa-search-minus fa-xs"></i>
                  <input type="range" min="0.2" max="1.8" value={this.state.rangeValue} onChange={this.handleChangeScale} step="0.01"/>
                  <i className="fas fa-search-plus fa-xs"></i>
                </div>
                <div>
                  <div onClick={this.handleImageUpload} id="own" className="tile">
                    <label htmlFor={"file-input" + this.props.id} className="btn btn-primary active">
                        {((this.state.imageUploaded === true) ? "Change image" : "Add your image")}
                    </label>
                    <input id={"file-input" + this.props.id} className="file-input" type="file" name="name" accept="image/png, image/jpeg" onChange={this.handleImageChange} />            
                  </div>
                  <a href="#" className={"btn btn-primary " + ((this.state.imageUploaded === true) ? "active": "")} onClick={this.handleClick}>Download PNG</a>
                </div>
              </div>
            </div>
          </div>

        );
  }

}

export default Post